.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

body {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  color: white;
}

button {
  margin-left: 10px;
}
li {
  list-style: none;
}

.col-sm-4 {
  margin-bottom: 20px;
}

.fila {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.card {
  height: 350px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
